import { Key } from "./constants";
const isMac = () => navigator.platform.toUpperCase().indexOf("MAC") >= 0;
export const isPrintable = (keyInfo) => {
    let isOneChar = keyInfo.name.length === 1;
    let hasBrowserShortcutKey;
    if (isMac()) {
        hasBrowserShortcutKey = keyInfo.modifiers.indexOf(Key.META) > -1;
    }
    else {
        const alt = keyInfo.modifiers.indexOf(Key.ALT) > -1;
        const ctrl = keyInfo.modifiers.indexOf(Key.CTRL) > -1;
        hasBrowserShortcutKey = alt || ctrl;
    }
    // Older browsers use these for the 'key' element of KeyboardEvents for the numpad.
    const numpadKeys = [Key.NUMPAD_ADD, Key.NUMPAD_SUBTRACT, Key.NUMPAD_MULTIPLY, Key.NUMPAD_DIVIDE];
    const isNumpad = numpadKeys.indexOf(keyInfo.name) > -1;
    isOneChar = isOneChar || isNumpad;
    return (isOneChar && !hasBrowserShortcutKey);
};
export const getPressedModifiers = (event) => {
    const modifierKeys = [Key.META, Key.CTRL, Key.SHIFT, Key.ALT];
    return modifierKeys.filter((key) => event[key]);
};
export const getHistoryKey = () => {
    return isMac() ? Key.META : Key.CTRL;
};
